import styled from 'styled-components';

import { Link } from 'react-router-dom';


export const PricingSection = styled.div`
  padding: 100px 0 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #fff;
  margin-top: 60px;
`;

export const PricingWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;

  @media screen and (max-width: 960px) {
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

export const PricingHeading = styled.h1`
  color: #3d3eba;
  font-size: 48px;
  margin-bottom: 24px;
`;

export const PricingContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  @media screen and (max-width: 960px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
`;

export const PricingCard = styled(Link)`
  background: #e1f1ff;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
  width: 800px;
  height: 500px;
  text-decoration: none;
  border-radius: 4px;

  &:nth-child(even) {
    margin: 24px;
  }

  &:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }

  @media screen and (max-width: 960px) {
    width: 90%;

    &:hover {
      transform: none;
    }
  }
`;

export const PricingCardInfo = styled.div`
  display: flex;
  flex-direction: column;
  height: 500px;
  padding: 24px;
  align-items: center;
  color: #fff;
`;

export const PricingCardIcon = styled.img`
  margin: 24px 0;
  height: 160px;
  width: 160px;
  margin-bottom: 10px;
`;

export const PricingCardPlan = styled.h3`
  margin-bottom: 5px;
  font-size: 24px;
  color: #3d3eba;
`;

export const PricingCardCost = styled.h4`
  font-size: 40px;
`;

export const PricingCardLength = styled.p`
  font-size: 14px;
  margin-bottom: 24px;
  
  
`;

export const PricingCardFeatures = styled.ul`
  margin: 16px 0 32px;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: left;
  color: black;
  justify-content: center;
  align-items: center;
  
`;

export const PricingCardFeature = styled.li`
  margin-bottom: 10px;
  width: 50%;
  text-align: center;
`;

export const HeaderContainer = styled.div`
 background-color: #3d3eba;
 height: 40vh;
 width: 100%;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 object-fit: contain;
`

export const UslugeH1 = styled.h1`
 color: #fff;
 font-size: 64px;
 margin-top: -120px;
 font-family: 'Montserrat';
 margin-left: 10px;

 @media screen and (max-width: 960px) {
     font-size: 55px;
     margin-top: -130px;
     margin-left: 10px;
 }
`




